import '../App.css';
import { Button, Table } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';


const Productos = (props) => {
    return ( 
      
        <div className="Product">
          <h2>Productos</h2>
        {         
        
             <Table striped bordered hover>
               
            <thead>
              <tr>
                <th>C贸digo</th>
                <th>Descripci贸n</th>
                <th>Precio</th>
                <th></th>
              </tr>
            </thead>
              <tbody>
              {
              props.lista.map((a,index)=>
                <tr key={index}>
                    <td>{a.codigo}</td>
                    <td>{a.descripcion}</td>
                    <td>${(a.precio).toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,')}</td>
                    <td><Button onClick={()=>props.agregar(a,a.precio)}variant="success" >Agregar</Button></td>
                </tr>
              )
            }
              </tbody>
            </Table>
          }
         <h3>Total: ${(props.total).toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,')}</h3>
          </div>
     );
}

export default Productos;